













































































































































































































































































































































































































































































































































































































































































import { Vue, Component } from 'vue-property-decorator'
import { getBasicInfo, sendApplication } from 'utils/api'

@Component({})
export default class Application extends Vue {
  form: any = {
    firstName: '',
    lastName: '',
    sex: 0,
    birthDate: '',
    MTPNumber: '',
    IDCardNumber: '',
    highSchool: '',
    graduationYear: '',
    address: '',
    postalCode: '',
    phoneNumber: {
      home: '',
      mobile: '',
      fax: ''
    },
    curriculumChoices: {
      firstChoice: '',
      secondChoice: '',
      thirdChoice: '',
      fourthChoice: ''
    },
    artOrSci: 0,
    primarySchool: {
      name: '',
      region: '',
      startDate: '',
      endDate: ''
    },
    juniorSchool: {
      name: '',
      region: '',
      startDate: '',
      endDate: ''
    },
    seniorSchool: {
      name: '',
      region: '',
      startDate: '',
      endDate: ''
    },
    familyMember1: {
      relationship: '',
      name: '',
      organization: '',
      occupation: '',
      phoneNumber: ''
    },
    familyMember2: {
      relationship: '',
      name: '',
      organization: '',
      occupation: '',
      phoneNumber: ''
    },
    familyMember3: {
      relationship: '',
      name: '',
      organization: '',
      occupation: '',
      phoneNumber: ''
    },
    gsatResult: {
      chinese: '',
      math: '',
      english: '',
      socials: '',
      sciences: ''
    },
    activities: {
      organization: '',
      award: '',
      attendingDate: ''
    }
  }
  needSimplification: boolean = false
  acceptAssignment: boolean = false

  mounted () {
    this.$nextTick(() => {
      getBasicInfo().then((res) => {
        this.form = (<any> Object).assign({}, res.data.form)
      }).catch((err) => {
        alert(err)
      })
    })
  }

  submitApplication () {
    sendApplication({
      firstName: this.form.firstName,
      lastName: this.form.lastName,
      sex: this.form.sex,
      birthDate: this.form.birthDate,
      MTPNumber: this.form.MTPNumber,
      IDCardNumber: this.form.IDCardNumber,
      highSchool: this.form.highSchool,
      graduationYear: this.form.graduationYear,
      address: this.form.address,
      postalCode: this.form.postalCode,
    }).then((res) => {
      this.$message({
        message: '提交成功',
        type: 'success'
      })
    }).catch((err) => {
      this.$message.error('提交失败')
    })
  }

  tempSaveApplication () {
    // 暂存
  }
}



